import { closeIcon } from "src/assets";
import { Dialog } from "../dialog"
import dayjs from "dayjs"
import { getSign } from "src/api"
import utc from "dayjs/plugin/utc"
import { useWriteContract, useReadContract, useBalance, useBlockNumber } from 'wagmi' 
import contractABI from 'src/assets/abi/abi.json'

import './checkin.css';

dayjs.extend(utc)

export const OnChainCheckIn = ({ visible, quest, onClose }) => {
    const { data: hash, error,  writeContract } = useWriteContract()
    const blockNumber = useBlockNumber() 
    const result = useBalance({
        address: '0x3693f48f2f19E354d9bFe550193b13282C0B08fe',
      })

    // console.log(hash, 'hash')
    // console.log(error, 'error')
    // console.log(blockNumber, 'blockNumberblockNumber')

    const { data: balance } = useReadContract({
        address: '0x3a34cd2234De4c1494Ffb6A995A81a67617b7aD0', 
        abi: contractABI,
        functionName: 'verifyAddr',
      })
    //   console.log(balance, 'verifyAddrverifyAddrverifyAddr')
    return (
        <Dialog visible={visible} onClose={onClose}>
            <div className="dlg-floow-twitter-quest on-chain-checkin-quest">
                <div className="fx-box welcome-header">
                            <div className="welcome-caption">
                                Click
                            </div>
                            <div style={{marginLeft:'auto',cursor:'pointer'}} onClick={onClose}>
                                <img src={closeIcon} alt=""/>
                            </div>
                </div>
                <div className="dlg-occi-main">
                    <div className="occi-title">
                        <div>*ON-chain check-in</div>
                    </div>
                    <div className="occi-content">
                        <div className="occi-timer">(UTC+0) {dayjs.utc().format('HH:mm:ss')}</div>
                    </div>
                </div>
                <div className="occi-footer fx-box fx-ai-c">
                    <div className="fx-box fx-ai-c">
                        <div>
                            <img src={quest?.rewardIcon || ''} width='36px' height='36px' alt=""/>
                        </div>
                        <div className="occi-points">
                            &nbsp;{quest?.reward}
                        </div>
                    </div>
                    <div className="occi-check-btn" onClick={async () => {
                        getSign(quest.taskId).then(res => {
                            if (res.code === 200) {
                                writeContract({ 
                                    address: '0x3a34cd2234De4c1494Ffb6A995A81a67617b7aD0', 
                                    abi: contractABI, 
                                    functionName: 'signIn', 
                                    args: [res.data.index, res.data.expired, res.data.signature], 
                                  }) 
                            }
                        })                                      
                    }} style={{marginLeft:'auto'}}>
                        Check-in
                    </div>
                </div>
            </div>
        </Dialog>
    )
}
