import React, {useState,useEffect} from "react";

import "./slider.css";

export default function Slider(props){
    const {sliderWidth,maxNumber,current}=props; // current/maxNumber=移动距离/sliderWidth;

    const [myRef, setMyRef] = useState(null);

    const [width, setWidth] = useState(10);
    const [sliderLeft,setSliderLeft]=useState("0em");

    useEffect(()=>{
        const max=sliderWidth/16;
        const pix=max/maxNumber;
        let sliderSize=pix*current;
        if(max-sliderSize<=0.25){
            setSliderLeft((max-0.5) +"em")
        }else{
            setSliderLeft(sliderSize + "em")
        }
        setWidth((parseFloat(sliderSize)+0.3)+"em");

        

    },[current]);
   
    // 滑条按钮移动函数
    function move(e){
        const target = e.target;
        const x = e.pageX;
        const max=sliderWidth/16;
        const left = parseFloat(target.style.left);
        myRef.onmousemove = function (event) {
            //console.log(left);
            if(left>=-0.5&&left<=max){
                const left2 = left + (event.pageX - x)/16;
                //改变滑块位置
                if(left2<=-0.5){
                    //target.style.left =0+"em";
                    setSliderLeft(0+"em")
                }else if(left2>=max){
                    console.log('max');
                    //target.style.left = (max-0.5) +"em";
                    setSliderLeft((max-0.5) +"em")
                }else{
                    //target.style.left = left2 + "em";
                    console.log('else',left2);
                    if(max-left2<=0.25){
                        setSliderLeft((max-0.5) +"em")
                    }else{
                        setSliderLeft(left2 + "em")
                    }
                   
                }
                // 改变滑条长度
                setWidth((parseFloat(target.style.left)+0.5)+"em");
            }

           
        }
    }
    // 清除移动
    function clearMove(){
        myRef.onmousemove = null;
    }

    return (
        <div className={"slider-containers"} style={{width:sliderWidth+'px'}} ref={(c)=>{setMyRef(c)}} onMouseLeave={clearMove}
            onMouseUp={clearMove}>
            <div className={"slider-container"}>
                <div className={"slider"} style={{"width":width}}></div>
                <div className={"slider-btn "} onMouseDown={move} onMouseUp={clearMove} style={{"left":sliderLeft}}>
                    
                </div>
            </div>
        </div>
    )
}