import { connectX, disconnectX } from "src/api";
import "./header.css";
import {LogoImg,ArrowLogoImg,LoginBtnIcon,XLogoImg, arrowDownIcon, disconnectIcon, toLinkIcon} from 'src/assets'
import { Link, useNavigate } from "react-router-dom";
import { connect } from "react-redux";
import { useState } from "react";

const TwitterLink = (props)=>{
    const [showTwitterLink,setShowTwitterLink] =useState(false);

    return (
        <div className="toolbar-login-menu fx-box fx-ai-c" style={{position:'relative'}}>
            {/* <img className="toolbar-login-menu-x" src={XLogoImg} /> */}
            <span>Twitter</span>
            <img className="toolbar-login-menu-arrow" src={ArrowLogoImg} onClick={()=>{
                setShowTwitterLink(!showTwitterLink);
            }} />
            {
                showTwitterLink?
                <div className="twitter-links">
                    <div><a href="">@PlayAriaGame<img src={toLinkIcon}/></a></div>
                    <div><a href="">@inutan_x<img src={toLinkIcon}/></a></div>
                </div>:null
            }
        </div>
    )
}

const ConnectXButton  = (props)=>{
    return (
        <div className="toolbar-login-btn connect fx-box fx-ai-c fx-jc-c" onClick={()=>{
            connectX();
        }}>
            <span>CONNECT X</span>
        </div>
    )
}

const ConnectedXButton = connect(null,dispatch=>({dispatch}))((props)=>{
    const [showDisconnect,setShowDisconnect] = useState(false);
    console.log('[debug]profile:',props.profile);
    
    return (
        <div className="toolbar-login-btn connected fx-box fx-ai-c fx-jc-c" onClick={async ()=>{
        }}>
            <div className="connect-x-icon">
                <img className="x-avatar" src={props.profile?.profileImageUrl}></img>
            </div>
            <div className="x-name">@{props.profile?.name}</div>
            <div className="tl-btn-dropdown" onClick={()=>{
                    setShowDisconnect(!showDisconnect);
                }}>
                <img className="dropdown" src={arrowDownIcon}/>
            </div>
            {
                showDisconnect?<div className="x-disconnect" onClick={()=>{
                    setShowDisconnect(false);
                    disconnectX().then(ret=>{
                        props.dispatch({type:'LOGOUT'});
                    })
                }}>
                <img src={disconnectIcon} style={{marginRight:8}}/> 
                <span>Disconnect</span>
                </div>:null
            }
            
        </div>
    )
})
export const Header = connect(
    state=>{
        return {
            profile:state.profile
        }
    },
    dispatch=>({dispatch})
)((props) => {
    const navigate = useNavigate();
    return (
        <div className="toolbar full-r-w fx-box fx-jc-c">
            <div className="toolbar-box fx-box fx-jc-sb fx-ai-c">
                <div className="fx-box fx-ai-c toolbar-box-left layout-rel">
                    {/* <Link to='/app'><img src={LogoImg} className="toolbar-logo" onClick={()=>{
                        
                    }}/></Link> */}
                    <div className="toolbar-logo-text">Aria</div>
                    <div className="toolbar-slider-btn fx-box fx-ai-c fx-jc-sb">
                        <span>MINT</span>
                        <img src={ArrowLogoImg} />
                    </div>
                    <div className="toolbar-promot layout-abs fx-box fx-ai-c">
                        <span className="toolbar-promot-text">Coming Soon</span>
                    </div>
                </div>

                <div className="toolbar-box-right fx-box fx-ai-c">
                    <TwitterLink/>
                    <a href="https://discord.gg/playariagame" target="_blank" style={{textDecoration:'none'}}><div className="toolbar-login-btn discord fx-box fx-ai-c fx-jc-sb">
                        <img src={LoginBtnIcon} />
                        <span className="toolbar-login-btn-text">DISCORD</span>
                    </div></a>
                    {
                        props.profile?<ConnectedXButton profile={props.profile}></ConnectedXButton>:<ConnectXButton/>
                    }
                    
                </div>
            </div>
            
        </div>
    )
})