import './players.css';
import React, {useState,useEffect} from "react";


import {
    PlayerDefault,
    PlayerPlay,
    PlayerPaused,
    PlayerNext,
    PlayerLast,
    PlayerNextActive,
    PlayerLastActive,
    PlayerVolume,
    PlayerVolumeActive} from '../../assets/index';
    import Slider from "./slider";

let counter=1;
let audioLong=0;
let  inter=null;
export const AudioControl=(props)=>{

       const {audioList,current}=props;
       const [isplay,setIsplay]=useState(false); 
       const [duration,setDuration]=useState(0);
       const [audioRef, setAudioRef] = useState(null);
       const [timer,setTimer]=useState(0);
       const [text,setText]=useState("00:00");
       const getTimeSecound=(seconds)=>{
                if(seconds===-1||seconds===0||seconds===1){
                    return "00:00";
                }
                var hour = Math.floor(seconds / (60 * 60)); // 计算小时部分
                seconds %= (60 * 60); // 取余得到不足1小时的秒数
                
                var minute = Math.floor(seconds / 60); // 计算分钟部分
                seconds %= 60; // 取余得到不足1分钟的秒数
                
                return [minute, seconds].map(num => num < 10 ? '0' + num : num).join(':').substring(0,5);
       }
       useEffect(()=>{
          var $timer=audioLong-counter;
          setText(getTimeSecound($timer));
       },[counter])

       const updateAudioProgress=()=>{
                inter=setInterval(()=>{
                    if(counter!==0&&audioLong!==0&&(audioLong-counter<0.5)){
                        clearInterval(inter);
                        audioRef.pause();
                     }else{
                        counter=counter+1;
                        setTimer(counter);
                     }
                 },1000)
       }
       return (
        <div className='custom-layout' style={{...props.style}}>
                {/* 组件本身 */}
                <div className="custom-audio">
                    <div className='custom-audio-dash layout-abs'>
                          <div className='fx-box fx-jc-sb'>
                            <div className='layout-ml8 fx-box'>
                                 <img 
                                    mode="cover"
                                    className='custom-audio-cover' 
                                    src={current.img}/>
                                    <div className='custom-audio-info'>
                                        <div className='custom-audio-info-title'>{current.title}</div>
                                        <div className='custom-audio-info-desc'>{current.desc}</div>
                                    </div>
                            </div>
                            
                             <div className='custom-audio-controls fx-box fx-ai-c layout-mr18'>
                                 <div className='custom-audio-controls-last layout-mr10'>
                                    <img className='basic-icon20' alt="" src={PlayerLast}/>
                                 </div>
                                 <div className='custom-audio-controls-play layout-mr10'>
                                    <img onClick={()=>{
                                        if(!isplay){
                                            let _audioLong=audioRef.duration;
                                            audioLong=_audioLong;//全局保存视频长度数据
                                            setDuration(audioLong);
                                            setIsplay(true);
                                            if(timer===0){
                                                setTimer(1);
                                            }else{
                                                setTimer((time)=>time+1);
                                            }
                                            updateAudioProgress();
                                            audioRef.play()
                                        }else{
                                            setTimer(counter);
                                            clearInterval(inter);
                                            setIsplay(false);
                                            audioRef.pause();
                                            console.log('counter 02',counter);
                                        }
                                       
                                    }} className='basic-icon20' alt="" src={!isplay?PlayerPlay:PlayerPaused}/>
                                 </div>
                                 <div className='custom-audio-controls-next layout-mr18'>
                                     <img className='basic-icon20' alt="" src={PlayerNext}/>
                                 </div>
                                 <div className='custom-audio-controls-volume'>
                                     <img className='basic-icon20' alt="" src={isplay?PlayerVolumeActive:PlayerVolume}/>
                                 </div>
                             </div>
                          </div>

                          <div className='costom-audio-progress fx-box fx-jc-sb fx-ai-c'>
                              <div className='progress-slider'>
                                 <Slider sliderWidth={250} maxNumber={duration} current={timer}/>
                              </div>
                              <div className='progress-timer layout-mr18'>-{text}</div>
                          </div>
                    </div>
                    <audio ref={(c)=>{
                        setAudioRef(c)
                    }}
                    src={current.link}
                    controls="controls"  
                    className='custom-hide-audio full-r-w full-r-h layout-abs'>
                          
                    </audio>
                </div>
        </div>
       );
}

export const Audio = AudioControl;